import { CreateEvent } from "./EventServices.model";
const token =
  "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9..eBqduXXjF8T91jCY9l85gTVy-iiV-3cWrhxDtBu_gug";

export const createEvent = async (data: CreateEvent) => {
  const myHeaders = new Headers();
  myHeaders.append("Authorization", `Bearer ${token}`);
  myHeaders.append("Content-Type", "application/json");
//   myHeaders.append("Access-Control-Allow-Headers", "*");
//   myHeaders.append("Access-Control-Allow-Headers", "Content-Type");

  const raw = {
    title: data.title,
    type: data.type,
    description: data.description,
    location: data.location,
    attendees: data.attendees,
    tripDate: data.tripDate,
    banner: data.banner,
    returnTrip: data.returnTrip,
    returnDate: data.returnDate,
    payment: data.payment,
  };

  const requestOptions: RequestInit = {
    method: "POST",
    headers: myHeaders,
    body: JSON.stringify(raw),
    redirect: "follow",
  };

  try {
    const res = await fetch(
      "https://api.test.shuttlers.africa/event/create",
      requestOptions
    );
    const result = await res.json();
    return result;
  } catch (error) {
    console.error("error", error);
  }
};

